.button {
    all: unset;
    display: inline-flex;
    align-items: center;
    padding: 6px 12px;
    font-size: 14px;
    line-height: 16px;
    min-height: 40px;
    border-right: 1px solid var(--colors-grey400);
    cursor: pointer;
    min-width: 240px;
}
.prefix {
    color: var(--colors-grey600);
    padding-right: var(--spacers-dp4);
}
.value {
    flex-grow: 1;
    padding-right: var(--spacers-dp4);
}
.button:disabled {
    cursor: not-allowed;
}
.button:disabled > .value {
    visibility: hidden;
}
